<template>
  <div id="app">
		<el-container>
			<el-aside width="200px" class="aside-main" :style="contentStyleObj">
				<v-aside></v-aside>
			</el-aside>
			<el-container>
				<el-header class="header-main">
					<div>
						<v-header></v-header>
					</div>
				</el-header>
				<el-container class="main-main">
						<router-view></router-view>
				</el-container>
			</el-container>
		</el-container>
  </div>
</template>

<script>
	import aside from './components/aside'
	import header from './components/header'
export default {
  name: 'App',
	data(){
		return{
			contentStyleObj:{
				height:'',
			},
			elmenuStyleObj:{
				height:'',
			},
			
		}
	},
  components:{
  	'v-aside':aside,
  	'v-header':header,
  },
	methods:{
		getHeight(){
			this.contentStyleObj.height=window.innerHeight-0+'px';
			this.elmenuStyleObj.height=window.innerHeight-104+'px';
		}
	},
	
	created(){
		window.addEventListener('resize', this.getHeight);
		this.getHeight()
	},
	
	destroyed(){
		window.removeEventListener('resize', this.getHeight)
	},
	mounted() {
		console.log(11);
	}
}
</script>

<style>
	@import url("style/common.css");
</style>



