<template>
	<div class="mainbody">
		<el-row>
			<el-col :span="8" class="main-left">
			  <div class="topdiv">部门</div>
			  <div class="main">
				  <el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick" node-key="id" default-expand-all :expand-on-click-node="false" :render-content="renderContent" class="treeDiv"></el-tree>
			  </div>
			</el-col>
			<el-col :style="contentStyleObj" :span="16" class="main-right">
				 <div class="topdiv">
					 <span>用户名：</span>
					 <el-input v-model="username" placeholder="请输入用户名"></el-input>
					 <span>手机号：</span>
					 <el-input v-model="username" placeholder="请输入手机号"></el-input>
					 <span>邮箱：</span>
					 <el-input v-model="username" placeholder="请输入邮箱"></el-input>
					 <el-button type="primary">查询</el-button>
				 </div>
				 <div class="main">
					 <el-button type="primary">+ 新建账号</el-button>
					 <el-table class="tableDiv" border :data="tableData3" style="width: 100%">
						<el-table-column type="selection" width="55"> </el-table-column>
						<el-table-column prop="user" label="用户名" width="120"></el-table-column>
						<el-table-column  prop="phone" label="手机号" width="145"> </el-table-column>
						<el-table-column  prop="email" label="邮箱" width="210"> </el-table-column>
						<el-table-column prop="role" label="角色" show-overflow-tooltip> </el-table-column>
						<el-table-column label="操作" width="100">
						  <template slot-scope="scope">
							<el-button @click="handleClick(scope.row)" type="text" size="small">修改</el-button>
							<el-button @click.native.prevent="deleteRow(scope.$index, tableData3)" type="text" size="small">删除</el-button>
						  </template>
						</el-table-column>
					  </el-table>
				 </div>
			</el-col>
		</el-row>
		
	</div>
</template>

<script>
	export default{
		name:'account',
		data(){
			return{
				contentStyleObj:{
					height:'',
					width:'',
				},
				username:'',
				phone:'',
				email:'',
				tableData3: [{
				  user: '欢欢',
				  phone: '15910853949',
				  email: '775262834@qq.com',
				  role: '客服、财务'
				},{
				  user: '小雪',
				  phone: '15910853949',
				  email: '1346729676@qq.com',
				  role: '管理员、调度'
				},{
				  user: '亚亚',
				  phone: '15910853949',
				  email: '775262834@qq.com',
				  role: '财务、结算'
				},{
				  user: '岚岚',
				  phone: '15910853949',
				  email: '775262834@qq.com',
				  role: '超级管理员'
				},{
				  user: '建儿',
				  phone: '15910853949',
				  email: '775262834@qq.com',
				  role: '超级管理员'
				},],
				data: [{
				  label: '上海点觉信息技术有限公司',
				  children: [{
					label: '产品部',
					children: [{
					  label: '用户体验部',
					  children: [{
						  label: 'UX交互科',
						  children: [{
							  label: 'UI设计组',
						  }]
					  }]
					}]
				  }]
				}, {
				  label: '上海奔腾物流有限公司',
				  children: [{
					label: '产品部',
					children: [{
					  label: '用户体验部',
					  children: [{
						  label: 'UX交互科',
						  children: [{
							  label: 'UI设计组',
						  }]
					  }]
					}]
				  }]
				}, {
				  label: '上海娃哈哈技术有限公司',
				  children: [{
					label: '产品部',
					children: [{
					  label: '用户体验部',
					  children: [{
						  label: 'UX交互科',
						  children: [{
							  label: 'UI设计组',
						  }]
					  }]
					}]
				  }]
				}],
				defaultProps: {
				  children: 'children',
				  label: 'label'
				}
			}
		},
		components:{
			
		},
		methods:{
			 //自定义内容
			 append(data) {
				const newChild = {  label: '新增加的公司伙伴', children: [] };
				if (!data.children) {
				  this.$set(data, 'children', []);
				}
				data.children.push(newChild);
			  },
		 
			  remove(node, data) {
				const parent = node.parent;
				const children = parent.data.children || parent.data;
				const index = children.findIndex(d => d.id === data.id);
				children.splice(index, 1);
			  },
			 renderContent(h, { node, data, store }) {
				return (
				  <span class="custom-tree-node">
					<span>{node.label}</span>
					<span>
					  <el-button size="mini" type="text" on-click={ () => this.append(data) }>···</el-button>
					  <el-button size="mini" type="text" on-click={ () => this.remove(node, data) }>+</el-button>
					</span>
				  </span>);
			  },
			handleClick(row) {
				// console.log(row);
			},
			deleteRow(index, rows) {
				rows.splice(index, 1);
			 },
			 handleNodeClick(data) {
				// console.log(data);
			 },
			 getHeight(){
			 	this.contentStyleObj.height=window.innerHeight-70+'px';
			 	this.contentStyleObj.width=window.innerWidth-587+'px';
			 },
		},
		created(){
			window.addEventListener('resize', this.getHeight);
			this.getHeight()
		},
		
		destroyed(){
			window.removeEventListener('resize', this.getHeight)
		}
	}
</script>

<style scoped>
	.custom-tree-node {
		flex: 1;
		display: flex;
		align-items: center;
		justify-content: space-between;
		font-size: 14px;
		padding-right: 8px;
	 }
	.tableDiv{
		margin-top: 10px;
	}
	.mainbody{
		width: 100%;
		text-align: left;	
		background: #fff;
	}
	.mainbody div.topdiv{
		width: 100%;
		overflow-x: hidden;
		padding-left: 15px;
		height: 52px;
		line-height: 52px;
		border-bottom: 1px solid #E8E8E8;
	}
	.main-left{
		width: 320px;
	}
	.main-right{
		border-left: 1px solid #E8E8E8;
	}
	.main-right .main{
		padding: 10px 8px;
		
	}
	.main-right .topdiv .el-input{
		display: inline-block;
		width:180px;
		margin-right:20px;
	}
	
	.main-right .topdiv .el-button{
		display: inline-block;
		padding: 7px 15px;
	}
	
</style>
<style>
	.main-right .topdiv .el-input__inner{
		height: 32px;
	}
	.el-tree-node__content{
		height: 40px!important;
	}
	.el-tree-node:focus>.el-tree-node__content{
		background: #E6F7FF;
	}
	.treeDiv .el-button--text{
		color: #999999;
		font-size: 20px;
		margin-right: 20px;
	}
	.treeDiv .custom-tree-node{
		width: 290px;
	}
 	.treeDiv .custom-tree-node>span:first-child{
 		float: left;
 		margin-top: 7px;
 	}
	.treeDiv .custom-tree-node>span:last-child{
		float: right;
		display: none;
	}
	.treeDiv .el-tree-node:focus>.el-tree-node__content .custom-tree-node>span:last-child{
		display: inline-block;
	}
</style>


