<template>
	<div>
		<el-scrollbar style="height: 100%;">
			<div class="logoDiv">
				<div>Logo</div>
			</div>
			<div class="systemName">
				<el-popover placement="right"  width="200" trigger="click"  v-model="visible2">
				  <div class="company">
					  <p @click="changeCompany($event)">上海点觉信息技术有限公司</p>
					  <p @click="changeCompany($event)">上海奔腾物流有限公司</p>
					  <p @click="changeCompany($event)">娃哈哈饮料公司</p>
				  </div>
				  <el-button class="showCompany" :style="comStyle" slot="reference">{{companyText}}</el-button>
				</el-popover>
			</div>
			<el-menu :style="elmenuStyleObj" default-active="1" class="el-menu-vertical-demo" background-color="#2F4156" text-color="#fff">
					<el-menu-item index="1">
						<template slot="title"><span>订单管理</span></template>
					</el-menu-item>
					<el-menu-item index="2">
						<template slot="title"><span>运单管理</span></template>
					</el-menu-item>
					<el-menu-item index="3">
						<template slot="title"><span>报表管理</span></template>
					</el-menu-item>
					<el-menu-item index="4">
						<template slot="title"><span>结算管理</span></template>
					</el-menu-item>
					<el-menu-item index="5">
						<template slot="title"><span>财务管理</span></template>
					</el-menu-item>
					<el-submenu index="6">
						<template slot="title"><span>资源管理</span></template>
						<el-menu-item-group>
						<el-menu-item index="6-1">客户信息</el-menu-item>
						</el-menu-item-group>
						<el-menu-item-group>
							<el-menu-item index="6-2">承运商信息</el-menu-item>
						</el-menu-item-group>
						<el-menu-item-group>
							<el-menu-item index="6-3">中转站管理</el-menu-item>
						</el-menu-item-group>
						<el-menu-item-group>
							<el-menu-item index="6-4">货物信息</el-menu-item>
						</el-menu-item-group>
						<el-menu-item-group>
							<el-menu-item index="6-5">中转站信息</el-menu-item>
						</el-menu-item-group>
						<el-menu-item-group>
							<el-menu-item index="6-6">司机信息</el-menu-item>
						</el-menu-item-group>
						<el-menu-item-group>
							<el-menu-item index="6-7">车辆信息</el-menu-item>
						</el-menu-item-group>
						<el-menu-item-group>
							<el-menu-item index="6-8">设备信息</el-menu-item>
						</el-menu-item-group>
						<el-menu-item-group>
							<el-menu-item index="6-9">设备信息</el-menu-item>
						</el-menu-item-group>
					</el-submenu>
					<el-submenu index="7">
						<template slot="title"><span>用户管理</span></template>
						<el-menu-item-group>
							<router-link to="/usercenter"><el-menu-item index="7-1">用户中心</el-menu-item></router-link>
						</el-menu-item-group>
						<el-menu-item-group>
							<router-link to="/"><el-menu-item index="7-2">用户设置</el-menu-item></router-link>
						</el-menu-item-group>
					</el-submenu>
					<el-submenu index="8">
						<template slot="title"><span>账号管理</span></template>
						<el-menu-item-group>
							<el-menu-item index="8-1">分公司信息</el-menu-item>
						</el-menu-item-group>
						<el-menu-item-group>
							<el-menu-item index="8-2">部门信息</el-menu-item>
						</el-menu-item-group>
						<el-menu-item-group>
							<router-link to="/account"><el-menu-item index="8-3">账号管理</el-menu-item></router-link>
						</el-menu-item-group>
						<el-menu-item-group>
							<el-menu-item index="8-5">角色权限信息</el-menu-item>
						</el-menu-item-group>
						<el-menu-item-group>
							<el-menu-item index="8-6">账号信息</el-menu-item>
						</el-menu-item-group>
					</el-submenu>
					<el-menu-item index="9">
						<template slot="title"><span>系统设置</span></template>
					</el-menu-item>
			</el-menu>				
		</el-scrollbar>
	</div>
</template>

<script>
	export default {
		name:'v-aside',
		data(){
			return{
				contentStyleObj:{
					height:'',
				},
				elmenuStyleObj:{
					height:'',
				},
				comStyle:{
					background:'#2F4156',
					color:'#fff',
					border:'none',
					
				},
				visible2: false,
				companyText:'上海点觉信息技术有限公司',
			}
		},
		methods:{
			getHeight(){
				this.contentStyleObj.height=window.innerHeight-0+'px';
				this.elmenuStyleObj.height=window.innerHeight-104+'px';
			},
			changeCompany(e){
				this.companyText=e.target.innerText;
				this.visible2=false;
			}
		},
		
		created(){
			window.addEventListener('resize', this.getHeight);
			this.getHeight()
		},
		
		destroyed(){
			window.removeEventListener('resize', this.getHeight)
		}
	}
</script>

<style>
	.el-popper[x-placement^=right]{
		margin-left: -9px;
	}
	.company p{
		cursor: pointer;
	}
</style>



