<template>
	<div>
		<div class="left"><i class="el-icon-menu"></i></div>
		<div class="right">
			<el-input placeholder="请输入运单号查询" prefix-icon="el-icon-search"></el-input>
			<span>
				<i class="el-icon-news"></i>
				<span>帮助中心</span>
			</span>
			<span class="user">
				<!-- <image src="../../image/default-icon.png" mode=""></image>
				<image :src="src1" mode=""></image> -->
				<!-- <span class="head"></span> -->
				<el-dropdown trigger="hover" v-show="islogin">
                    <span class="el-dropdown-link userinfo-inner">{{sysUserName}}</span>
                    <el-dropdown-menu slot="dropdown">
						<router-link to="/usercenter">
							<el-dropdown-item>用户中心</el-dropdown-item>
						</router-link>
						<router-link to="/">
							<el-dropdown-item>用户设置</el-dropdown-item>
						</router-link>
                        <el-dropdown-item divided @click.native="logoutFun">退出登录</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
				<router-link v-show="!islogin" to="/login">
					<span>登录</span>
				</router-link>
				
			</span>
		</div>
	</div>
</template>

<script>
	import logo from '@/assets/logo.png'
	export default {
		name:'v-header',
		data() {
			return {
				src:require('../image/default-icon.png'),
				src1:logo,
				sysUserName:'',
				islogin:false,
			};
		},
		 methods: {
			//折叠导航栏
			collapseFun: function() {
				this.collapsed = !this.collapsed;
			},
			showMenu(i, status){
			  this.$refs.menuCollapsed.getElementsByClassName('submenu-hook-' + i)[0].style.display = status ? 'block' : 'none';
			},
			//退出登录
			logoutFun: function() {
				var _this = this;
				this.$confirm('确认退出吗?', '提示', {
					//type: 'warning'
				}).then(() => {
					sessionStorage.removeItem('user');
					this.islogin=false;
				}).catch(() => {

				});
			},
			
		},
		mounted() {
			var user = sessionStorage.getItem('user');
			if (user) {
				this.islogin=true;
				this.sysUserName =  '魏雪';
			}
		}
	}
</script>

<style>
	.el-dropdown{
		cursor: default;
	}
	.user .head{
		display: inline-block;
		width: 26px;
		height: 26px;
		background: #D4D7D9;
		border-radius: 50%;
	}
</style>



